import React, { useState } from 'react';
import {Link} from 'gatsby';

const Rankings = ({id, datasource} : {id:string, datasource:any}) => { 
    const JSONData = datasource;

    const PageLayout = <section className="tf-section tf-item nopadding">
        <div className="container">
            <div className="tournament-section-header">
                <h6>Game stats</h6>
            </div>
            <div className="row">                
                <div className="col-md-4">
                    <div className="rankingCol">
                        <div className="center"><h5>Teams ranking</h5></div>      
                        <div className="ranking">
                            {JSONData.rankings.teams.sort((team1:any, team2:any)=> team1.points < team2.points ? 1 : -1).map((team:any) => (
                                <div className="row" key={"TeamRank" + team.name}>
                                    <div className="col-md-10 col-10">
                                        {team.name}
                                    </div>
                                    <div className="col-md-2 col-2">
                                        {team.points}
                                    </div>
                                </div>
                            ))}
                            <div className={JSONData.rankings.teams.length != 0 ? "hide": ""}>No data available</div>
                        </div> 
                    </div>             
                </div>
                <div className="col-md-4">
                    <div className="rankingCol">
                        <div className="center"><h5>Top scorers</h5></div>
                        <div className="ranking">
                            {JSONData.rankings.topscorers.sort((p1:any, p2:any)=> p1.points < p2.points ? 1 : -1).map((player:any) => (
                                <div className="row" key={"Scorer" + player.cshirt}>
                                    <div className="col-md-10 col-10">
                                        <Link to={'../players/' + player.cshirt}>C-Shirt #{player.cshirt}</Link>
                                    </div>
                                    <div className="col-md-2 col-2">
                                        {player.points}
                                    </div>
                                </div>
                            ))}
                            <div className={JSONData.rankings.topscorers.length != 0 ? "hide": ""}>No data available</div>
                        </div>  
                    </div>            
                </div>
                <div className="col-md-4">
                    <div className="rankingCol">
                        <div className="center"><h5>Top assistmen</h5></div>
                        <div className="ranking">
                            {JSONData.rankings.assistmen.sort((a1:any, a2:any)=> a1.assists < a2.assists ? 1 : -1).map((player:any) => (
                                <div className="row" key={"Assistman" + player.cshirt}>
                                    <div className="col-md-10 col-10">
                                        <Link to={'../players/' + player.cshirt}>C-Shirt #{player.cshirt}</Link>
                                    </div>
                                    <div className="col-md-2 col-2">
                                        {player.assists}
                                    </div>
                                </div>
                            ))}
                            <div className={JSONData.rankings.assistmen.length != 0 ? "hide": ""}>No data available</div>
                        </div>    
                    </div>          
                </div>
            </div>
        </div>
    </section>
    return(
        PageLayout
    );
};

export default Rankings;

